import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
// import App from './App';
import AppBurger from './AppBurger'
import registerServiceWorker from './registerServiceWorker';
import {Provider} from 'react-redux';
import {createStore, applyMiddleware, compose, combineReducers} from 'redux';
import burgerBuilderReducer from './store/reducers/burgerBuilder';
import orderReducer from './store/reducers/order';
import authReducer from './store/reducers/auth';
import thunk from 'redux-thunk';

const composeEnhancers = process.env.NODE_ENV === 'development' ? window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ : null || compose;

const reducers = combineReducers({
    burgerReducer: burgerBuilderReducer,
    orderReducer: orderReducer,
    authReducer: authReducer
});

// thunk middleware allows asynchronous code
const store = createStore(reducers, composeEnhancers(applyMiddleware(thunk)));

//ReactDOM.render(<App appTitle='React course'/>, document.getElementById('root'));
ReactDOM.render(<Provider store={store}><AppBurger appTitle='React course'/></Provider>, document.getElementById('root'));
registerServiceWorker();



