import React, { Component } from 'react';
import Aux from '../../hoc/Auxiliar';
import Burger from '../../components/Burger/Burger';
import BuildControls from '../../components/Burger/BuildControls/BuildControls';
import Modal from '../../components/UI/Modal/Modal';
import OrderSummary from '../../components/Burger/OrderSummary/OrderSummary';
import Spinner from '../../components/UI/Spinner/Spinner';
import withErrorHandler from '../../hoc/withErrorHandler/withErrorHandler';
import {connect} from 'react-redux';
import * as actions from '../../store/actions/index';
import axios from '../../axios-orders';
import {withRouter} from 'react-router-dom';

export class BurgerBuilder extends Component {
    state = {
        purchasing: false
    }

    componentDidMount () {
        this.props.onInitFetchIngredients();
    }

    checkoutClickHandler = () => {
        if (this.props.isAuthenticated) {
            this.setState({purchasing: true});
        }
        else {
            this.props.onSetAuthRedirectPath("/checkout");  // where the user should go after redirect
            this.props.history.push("/auth");
        }
    }
    
    checkoutCancelHandler = () => {
        this.setState({purchasing: false});
    }

    checkoutContinueHandler = () => {
        this.props.onInitPurchase();
        this.props.history.push("/checkout");
    }

    updatePurchaseState (ingredients) {
        const sum = Object.keys(ingredients).map(igKey => {
            return ingredients[igKey];
        })
        .reduce((sum, el) => {
            return sum + el;
        }, 0);
        return sum>0;
    }

    render () {
        const disabledInfo = {
            ...this.props.ingredients
        }
        for (let key in disabledInfo){
            disabledInfo[key] = disabledInfo[key] <= 0
        }

        let orderSummary = null;

        let burger = this.props.error ? <p>Error loading!</p> : <Spinner />;
        if (this.props.ingredients && this.props.price && this.props.ingredientPrices) {
            burger = 
            <Aux>
                <Burger ingredients={this.props.ingredients}/>
                <BuildControls
                ingredientAdded={this.props.addIngredient} ingredientRemoved={this.props.removeIngredient}
                disabled={disabledInfo} price={this.props.price} 
                purchasable={this.updatePurchaseState(this.props.ingredients)}
                showModal={this.checkoutClickHandler}
                ingredientPrices={this.props.ingredientPrices}
                isAuth={this.props.isAuthenticated}/>
            </Aux>;
            orderSummary = 
                <OrderSummary ingredients={this.props.ingredients} purchaseCanceled={this.checkoutCancelHandler} 
                    purchaseContinued={this.checkoutContinueHandler} price={this.props.price}/>;
        }

        return (
            <Aux>
                <Modal show={this.state.purchasing} modalClosed={this.checkoutCancelHandler}>
                    {orderSummary}
                </Modal> 
                {burger}
            </Aux>
        );
    }
}

// retrieve data from Redux's Store
const mapStateToProps = state => {
    return {
        ingredients: state.burgerReducer.ingredients,
        price: state.burgerReducer.totalPrice,
        ingredientPrices: state.burgerReducer.prices,
        error: state.burgerReducer.error,
        isAuthenticated: state.authReducer.token !== null
    };

}

// define actions to interact with Redux's Store
const mapDispatchToProps = dispatch => {
    return {
        addIngredient: (ingName) => dispatch(actions.addIngredient(ingName)),
        removeIngredient: (ingName) => dispatch(actions.removeIngredient(ingName)),
        onInitFetchIngredients: () => dispatch(actions.fetchIngredients()),
        onInitPurchase: () => dispatch(actions.purchaseInit()),
        onSetAuthRedirectPath: (path) => dispatch(actions.setAuthRedirectPath(path))
    };
}

export default withRouter(connect(mapStateToProps,mapDispatchToProps)(withErrorHandler(BurgerBuilder, axios)));


