import React from 'react';
import classes from './BuildControls.css';
import BuildControl from './BuildControl/BuildControl';

const  controls = [
    {label: 'Salad', type: 'salad'},
    {label: 'Cheese', type: 'cheese'},
    {label: 'Meat', type: 'meat'},
    {label: 'Bacon', type: 'bacon'}
];

const buildControls = (props) => {
    let price;
    const ingredientPrices = (type) => Object.keys(props.ingredientPrices).forEach((Iprice) => {
            if (Iprice === type) {
                price = props.ingredientPrices[Iprice];
            }
        })

    return (
        <div className={classes.BuildControls}>
            <p>Current price: <strong>{props.price.toFixed(2)} </strong></p>
            {controls.map((ctrl, i) => {
                ingredientPrices(ctrl.type);
                return (                
                    <BuildControl key={ctrl.label} label={ctrl.label} added={() => props.ingredientAdded(ctrl.type)} removed={() => props.ingredientRemoved(ctrl.type)}
                    disabled={props.disabled[ctrl.type]} price={price}/>
                );
            })}
            <button className={classes.OrderButton} disabled={!props.purchasable} onClick={props.showModal}>{props.isAuth ? "ORDER NOW!!" : "SIGN UP TO ORDER"}</button>
        </div>
    );
};

export default buildControls;




